@import "variables.scss";

.reviews {
  max-width: 1920px;
  margin-top: 90px;

  &__title-container {
    max-width: 1170px;
    margin: 0 auto;

    @media (max-width: 1170px) {
      padding-left: 10px;
      padding-right: 10px;
    }
  }

  &-bg {
    margin-top: 30px;
    background-color: $color-pink;
  }

  &-container {
    max-width: 1170px;
    margin: 0 auto;

    @media (max-width: 1170px) {
      padding-left: 10px;
      padding-right: 10px;
      overflow: hidden;
    }
  }

  &__main {
    padding: 90px 0;
  }

  &__slider-indicators {
    margin-bottom: -30px;
  }

  &__review {
    max-width: 1170px;
    max-height: 300px;
  }

  &__review-container {
    display: flex;
    justify-content: space-between;
    margin: 0 100px;
  }

  &__review-body {
    max-width: 280px;
    background-color: #fff;
    border-radius: 7px;
    padding: 30px 25px 25px;
  }

  &__review-text {
    width: 100%;
    font-size: 12px;
    color: #929292;
  }

  &__review-sign {
    font-size: 12px;
    color: #444444;
    padding-top: 25px;
    font-weight: 400;
  }
}